import React, { useEffect, useState } from 'react';
import Charactor from "./Charactor"
import "../index.css"

let trigTime = 0;
function Insert(props) {
    const [data, setData] = useState("");
    const [focus, setFocus] = useState(false);

    const inp = (e) => {
        let m = e.target.value
        setData(m)
        props.func(m);
    }

    useEffect(() => {
        if (trigTime < 2)
            setData(props.preset)
        trigTime++;
    })

    const renderInp = (a) => {
        if (a === true && props.active === true) {
            if(data==="请输入你的学号" ||data==="同学 A"){
                setData("");
            }
            return (
                <div>
                    <input
                        autoFocus
                        value={data}
                        style={{
                            position: "absolute",
                            top: 0,
                            width: props.w,
                            height: 6*props.ht,
                            background: "blue",
                            opacity: 0
                        }}
                        onChange={(e) => inp(e)}
                    >
                    </input>
                </div>
            )
        }
        return (null);
    }

    const listRender = (a) => {
        let pa = a.split("");
        //let max = props.w.replace("vh","");
        let max = props.ht * 20 / pa.length;
        //max = max+"vh";
        let rdr = pa.map((item, key) => { return (<Charactor w={item} max={max} />) })
        return (rdr);
    }

    const transform = () => {
        if (props.active) {
            return (
                <div>
                    <div
                        style={{
                            width: props.w,
                            height: 6 * props.ht
                        }}
                    >
                        <div
                            style={{
                                display: 'flex',
                                flexDirection: "row",
                                alignItems: "end",
                                top: 0,
                                width: props.w,
                                height: 6 * props.ht,
                                opacity: 1,
                                color:"gray",
                                opacity:0.6,
                                fontSize: 10,
                                fontFamily:"MyFont",
                            }}
                            onClick={() => setFocus(true)}
                        >
                            {listRender(data)}
                        </div>
                        {renderInp(focus)}
                    </div>
                    <div
                        style={{
                            width: props.w,
                            height: 2 * props.ht
                        }}
                    >
                        <div style={{ position: "absolute", left: "0px", bottom: "0px", fontSize: 1 * props.ht }}>{props.A}</div>
                        <div style={{ position: "absolute", right: "0px", bottom: "0px", fontSize: 1 * props.ht }}>{props.B}</div>
                    </div>
                </div>
            )
        }else{
            return(
                <div style={{position:"absolute"}}>
                    <div
                        style={{
                            top:0,
                            width: props.w,
                            height: 1 * props.ht
                        }}
                    >
                        <div style={{ position: "absolute", left: "0px", top: "0px", fontSize: 1* props.ht }}>{props.A}</div>
                        <div style={{ position: "absolute", right: "0px", top: "0px", fontSize: 1* props.ht }}>{props.B}</div>
                    </div>
                    <div
                        style={{
                            top:2 * props.ht,
                            width: props.w,
                            height: 4 * props.ht
                        }}
                    >
                        <div
                            style={{
                                display: 'flex',
                                flexDirection: "row",
                                alignItems: "top",
                                top: 2 * props.ht,
                                width: props.w,
                                height: 6 * props.ht,
                                color:"gray",
                                opacity:0.6,
                                fontSize: 10,
                            }}
                            onClick={() => setFocus(true)}
                        >
                            {listRender(data)}
                        </div>
                        {renderInp(focus)}
                    </div>
                </div>
            );
        }
    }

    return (
        <div
            style={{
                position: "absolute"
            }}
        >
            {transform()}
        </div>
    )
}
export default Insert;