import React from 'react';
import InsertBox from '../components/InsertBox'
import AudioAnimation from '../components/AudioAnimation';
import { animated, Spring } from '@react-spring/web'
import axios from 'axios';
import "../index.css"

let server = '121.40.159.180:3000';
let name = "";
let ID = "";
let ht = 0.0;

class Login extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            state: '',
            content: 'NULL',
            newPos: false,
        }
    }

    componentWillMount(){
        ht = window.innerWidth > window.innerHeight * 9 / 16?window.innerHeight:window.innerWidth*16/9;
        ht = ht/100;
    }

    async getFromServer(name, ID) {
        let data = new URLSearchParams()
        data.append('name', '琪露诺');
        data.append('cid', '3200602049');
        axios.post("http://" + server + "/api/voice/getStudent", data)
            .then(
                res => {
                    this.setState({
                        ...this.state,
                        state: res.data.state,
                        content: res.data.state === 9 ? 'NULL' : res.data.content,
                    }, () => console.log(this.state))
                })
            .catch(err => console.log("Couldn't fetch data. Error: " + err))
    }

    async soundTo(val) {
        let data = new URLSearchParams()
        data.append('name', '琪露诺');
        data.append('cid', '3200602049');
        axios.post("http://" + server + "/api/voice/getStudent", data)
            .then(
                res => {
                    this.setState({
                        ...this.state,
                        state: res.data.state,
                        content: res.data.state === 9 ? 'NULL' : res.data.content,
                    }, () => console.log(this.state))
                })
            .catch(err => console.log("Couldn't fetch data. Error: " + err))
    }

    async test() {
        let a = this.state.newPos;
        this.getFromServer(name, ID)
        a = !a;
        this.setState({
            ...this.state,
            newPos: a,
        })
    }

    getName(i) {
        name = i;
    }

    getId(i) {
        ID = i;
    }

    render() {
        return (
            <div style={{ width: window.innerWidth, height: window.innerHeight}}>
                <div style={{
                    position: "absolute",
                    left: window.innerWidth > window.innerHeight * 9 / 16 ? window.innerWidth * 0.5 - (window.innerHeight * 9 / 16)*0.5 : 0,
                    width: window.innerWidth > window.innerHeight * 9 / 16 ? window.innerHeight * 9 / 16 : window.innerWidth,
                    height: window.innerHeight,
                    background: "rgba(20,20,20,0.0)",
                }}>
                    <Spring
                        from={{
                            top: 21*ht,
                            left: 0*ht,
                        }}
                        to={{
                            top: this.state.newPos ? 13.4*ht : 22*ht,
                            left: this.state.newPos ? 18.125*ht : 18.125*ht,
                        }}
                    >
                        {styles => (
                            <animated.div style={{ position: "absolute", ...styles }}>
                                <InsertBox w={20*ht} h={5*ht} A="姓名" B="NAME" ht = {ht} preset={this.state.newPos ? name : "同学 A"} active={!this.state.newPos} func={this.getName} />
                            </animated.div>
                        )}
                    </Spring>

                    <Spring
                        from={{
                            top: 38*ht,
                            left: 0*ht,
                        }}
                        to={{
                            top: this.state.newPos ? 25*ht : 42*ht,
                            left: this.state.newPos ? 18.125*ht : 18.125*ht,
                        }}
                    >
                        {styles => (
                            <animated.div style={{ position: "absolute", ...styles }}>
                                <InsertBox w={20*ht} h={5*ht} A="学号" B="STUDENT ID" ht = {ht} preset={this.state.newPos ? ID : "请输入你的学号"} active={!this.state.newPos} func={this.getId} />
                            </animated.div>
                        )}
                    </Spring>

                    <div style={{ position: 'absolute', top: 77*ht, left: 23.125*ht }}>
                        {<AudioAnimation display={this.state.newPos} r={(window.innerWidth > window.innerHeight * 9 / 16?window.innerHeight:window.innerWidth*16/9)*0.1} />}
                        {this.state.newPos ?
                            <div style={{ display: "flex", flexDirection: "column" }}>
                                <h1 style={{ fontSize: 1.0*ht, textAlign: "center" }}>按住五秒</h1>
                                <h1 style={{ fontSize: 1.0*ht, textAlign: "center" }}>喊出自己</h1>
                            </div>
                            :
                            null
                        }
                    </div>
                    <div>
                        {this.state.newPos ?
                            <Spring
                                from={{
                                    opacity: 0,
                                }}
                                to={{
                                    opacity: 1
                                }}
                            >
                                {styles => (
                                    <animated.div style={{ position: "absolute", top: 47.6*ht, left: 25.625*ht, width: 56.25*ht, height: 5*ht, ...styles, alignItems: "center", justifyContent: "center" }}>
                                        <div
                                            style={{ display: "flex", width: 5*ht, height: 10*ht, alignItems: "center", justifyContent: "center" }}
                                        >
                                            <h1 style={{ color: "black", fontSize: 7*ht }}>{this.state.content["class"]}</h1>
                                        </div>

                                        <div
                                            style={{ display: "flex", width: 5*ht, height: 5*ht, background: "rgba(127,71,221,1)", borderRadius: "50%", alignItems: "center", justifyContent: "center" }}
                                        >
                                            <h1 style={{ color: "white" }}>{this.state.content["id"]}</h1>
                                        </div>

                                        <div
                                            style={{ display: "flex", position: "absolute", left: -6.5*ht, width: 18*ht, height: 4*ht, alignItems: "center", justifyContent: "center" }}
                                        >
                                            <h1 style={{ fontSize: 1.0*ht, left: 0, letterSpacing: "0.75" }}>序号</h1>
                                            <h1 style={{ fontSize: 1.0*ht, right: 0 }}>SERIAL NUMBER</h1>
                                        </div>
                                    </animated.div>
                                )}
                            </Spring>
                            :
                            null
                        }
                    </div>
                    <div>
                        {!this.state.newPos ?
                            <div
                                style={{ display: "flex", position: "absolute", top: 69.7*ht, left: 25.375*ht, width: 5.5*ht, height: 3*ht, alignItems: "center", justifyContent: "center" }}
                                onClick={() => this.test()}
                            >
                                提交
                            </div>
                            :
                            null
                        }
                    </div>
                </div>
            </div>
        )
    }
}
export default Login;